.login{
  width: 90%;
  max-width: 700px;
  margin: 10px auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.login .title{
  text-align: center;
}
.login .logo{
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 10px 0;
}
.login input{
  font-size: 18px;
  height: 40px;
  margin: 5px 0;
}
.login .Sign-in{
  color: #f4f4f4;
  background:-webkit-gradient(linear,right top, left top,from(rgba(63,205,235,1)),to(rgba(188,226,158,1)));
  background:linear-gradient(-90deg,rgba(63,205,235,1),rgba(188,226,158,1));
  width: 100%;
  height: 40px;
  margin-top: 5px;
  border: none;
  outline: none;
  border-radius: 10px;
  cursor: pointer;
}
.login .Sign-in:active{
  background: -webkit-gradient(linear,right top, left top,from(rgba(188,226,158,1)),to(rgba(63,205,235,1)));
  background: linear-gradient(-90deg,rgba(188,226,158,1),rgba(63,205,235,1));
  -webkit-box-shadow: 1px 1px 1px #bebebe;
          box-shadow: 1px 1px 1px #bebebe;
}
.login .problem{
  margin-top: 20px;
}
.login .problem span{
  color: #FFA800;
}
.login .manager{
  font-size: 13px;
  color: #fdd930;
  background-color: rgba(45,161,109,0.8);
  padding: 5px;
  position: absolute;
  right: 0;
  top: 110px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}

